import type {
  MoSerializationPlugin, MoServerSerializationPlugin,
} from '@ali/4ever-cangjie';
import jsonMLToValue from './jsonMLToValue';
import valueToJsonML from './valueToJsonML';
import htmlToJsonML from './htmlToJsonML';
import jsonMLToHTML from './jsonMLToHTML';

export default function createSerializer(configs): MoSerializationPlugin {
  return {
    jsonMLToValue,
    valueToJsonML,
    htmlToJsonML,
    jsonMLToHTML,
  };
}

export function createServerSerializationPlugin(): MoServerSerializationPlugin {
  return {
    valueToJsonML,
    jsonMLToValue,
  };
}
